/**
 * MainHome Ï†ÑÏö© Ïä§ÌÑ∞Îîî Ïπ¥ÌÖåÍ≥†Î¶¨ Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå
 */
export const useMainHomeStudies = (params = {}) => {
  return useQuery({
    queryKey: ["mainHome", "studies", "grouped", params],
    queryFn: async () => MOCK_STUDY_CATEGORIES,
    staleTime: Infinity,
    select: (data) => {
      const categoryMap = {
        coding: { label: "ÏΩîÎî©", key: "coding" },
        design: { label: "ÎîîÏûêÏù∏", key: "design" },
        video: { label: "ÏòÅÏÉÅÌé∏Ïßë", key: "video" },
      };

      return Object.entries(categoryMap).map(([key, config]) => {
        const categoryData = data?.[key];
        const users = categoryData?.content || [];

        return {
          label: config.label,
          key: key,
          count: users.length,
          users: users,
        };
      });
    },
  });
};

